import { NgModuleRef, StaticProvider } from '@angular/core';
import { BackbaseConnector } from '@backbase/foundation-ang/core';
import { ExternalServices, ÉµEXTERNAL_SERVICES as EXTERNAL_SERVICES } from '@backbase/foundation-ang/web-sdk';
/**
 * @deprecated Will be removed in v7.0.0
 */
export type CreateNgModule = (extraProviders: Array<StaticProvider>) => Promise<NgModuleRef<any>>;

/**
 * @deprecated Will be removed in v7.0.0
 */
export type StartSingleApp = (externalServices?: ExternalServices) => Promise<BackbaseConnector>;

/**
 * @deprecated Will be removed in v7.0.0
 * Exposes Angular Single Page Application bootstrap method `window.BB.startSingleApp`
 * @param createNgModule Function that accepts StaticProviders and returns NgModuleRef
 * @usageNotes
 * ### Example
 *
 * ```
 * window.BB.startSingleApp = registerSingleApp(createNgModule);
 *
 * window.BB.startSingleApp(webSdkServices, staticProviders)
 *   .then((bbConnector: BackbaseConnector) => bbConector.bootstrap(item, itemLocation));
 * ```
 */
export function registerSingleApp(createNgModule: CreateNgModule): StartSingleApp {
  const startSingleApp: StartSingleApp = (externalServices: ExternalServices = {}): Promise<BackbaseConnector> => {
    const providers = [
      {
        provide: EXTERNAL_SERVICES,
        useValue: externalServices,
      },
    ];
    return createNgModule(providers).then(
      (moduleRef: NgModuleRef<any>): BackbaseConnector => moduleRef.injector.get<BackbaseConnector>(BackbaseConnector),
    );
  };
  window.BB = window.BB || {};
  Object.assign(window.BB, { startSingleApp });
  return startSingleApp;
}

declare global {
  interface Window {
    BB: { [key: string]: any };
  }
}
